/* eslint-disable */
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || '';

export default function ManagerDashboard() {
  const [orders, setOrders] = useState([]);
  const [decorOrders, setDecorOrders] = useState([]);  //  
  const [issueCards, setIssueCards] = useState([]);  //  
  const [loading, setLoading] = useState(true);
  const [financeData, setFinanceData] = useState({ revenue: 0, deposits: 0 });
  const [cleaningStats, setCleaningStats] = useState({ repair: 0 });
  
  //    
  const [showAllAwaiting, setShowAllAwaiting] = useState(false);
  const [showAllReturns, setShowAllReturns] = useState(true);  //    
  const [searchQuery, setSearchQuery] = useState('');
  const [statusFilter, setStatusFilter] = useState('');
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  //   
  useEffect(() => {
    const userData = localStorage.getItem('user');
    if (userData) {
      setUser(JSON.parse(userData));
    }
  }, []);

  //     
  const handleCancelByClient = async (orderId, orderNumber) => {
    const reason = prompt(`  ${orderNumber}?\n\n   ():`);
    if (reason === null) return; // User clicked Cancel
    
    if (!confirm(`     ${orderNumber}?\n\n     .`)) {
      return;
    }
    
    try {
      const response = await fetch(`${BACKEND_URL}/api/decor-orders/${orderId}/cancel-by-client`, {
        method: 'POST',
        mode: 'cors',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          reason: reason || '   '
        })
      });
      
      if (response.ok) {
        alert('  .  .');
        fetchAllData(); //  
      } else {
        const error = await response.json();
        alert(` : ${error.detail || '   '}`);
      }
    } catch (error) {
      console.error('Error cancelling order:', error);
      alert(` : ${error.message}`);
    }
  };
  
  //    
  const handleArchiveOrder = async (orderId, orderNumber) => {
    if (!confirm(`  ${orderNumber}?\n\n     .`)) {
      return;
    }
    
    try {
      const response = await fetch(`${BACKEND_URL}/api/decor-orders/${orderId}/archive`, {
        method: 'POST',
        mode: 'cors'
      });
      
      if (response.ok) {
        alert('  ');
        fetchAllData(); //  
      } else {
        const error = await response.json();
        alert(` : ${error.detail || '  '}`);
      }
    } catch (error) {
      console.error('Error archiving order:', error);
      alert(` : ${error.message}`);
    }
  };

  //     
  const handleDateUpdate = async (orderId, issueDate, returnDate) => {
    try {
      const response = await fetch(`${BACKEND_URL}/api/orders/${orderId}`, {
        method: 'PUT',
        mode: 'cors',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          issue_date: issueDate,
          return_date: returnDate
        })
      });

      if (response.ok) {
        const updatedOrder = await response.json();
        console.log('[Dashboard]  Order dates updated:', orderId);
        
        //   
        setOrders(prevOrders => 
          prevOrders.map(o => o.id === orderId ? updatedOrder : o)
        );
        
        return updatedOrder;
      } else {
        throw new Error('Failed to update dates');
      }
    } catch (error) {
      console.error('[Dashboard] Error updating dates:', error);
      throw error;
    }
  };

  //     
  const fetchAllData = () => {
    console.log('[Dashboard]  Loading orders for today...');
    
    //       (    OpenCart)
    fetch(`${BACKEND_URL}/api/orders?status=awaiting_customer`, {
      method: 'GET',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    })
    .then(res => res.json())
    .then(data => {
      console.log('[Dashboard] Orders awaiting confirmation:', data.orders?.length || 0);
      setOrders(data.orders || []);
    })
    .catch(err => console.error('[Dashboard] Error loading orders:', err));
    
    //       
    fetch(`${BACKEND_URL}/api/decor-orders?status=processing,ready_for_issue,issued,on_rent,shipped,delivered,returning`, {
      method: 'GET',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    })
    .then(res => res.json())
    .then(data => {
      setDecorOrders(data.orders || []);
      setLoading(false);
    })
    .catch(err => {
      console.error('[Dashboard] Error loading decor orders:', err);
      setLoading(false);
    });
    
    //  Issue Cards ( )
    fetch(`${BACKEND_URL}/api/issue-cards`, {
      method: 'GET',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    })
    .then(res => res.json())
    .then(data => {
      console.log('[Dashboard] Issue cards:', data.length);
      setIssueCards(data);
    })
    .catch(err => console.error('[Dashboard] Error loading issue cards:', err));
    
    //   (  )
    fetch(`${BACKEND_URL}/api/manager/finance/summary`, {
      method: 'GET',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    })
    .then(res => res.json())
    .then(data => {
      console.log('[Dashboard] Finance summary:', data);
      setFinanceData({
        revenue: data.total_revenue || 0,  //  (payment completed)
        deposits: data.deposits_count || 0  //    
      });
    })
    .catch(err => console.error('[Dashboard] Error loading finance:', err));
  };

  useEffect(() => {
    fetchAllData();
    
    //     
    fetch(`${BACKEND_URL}/api/product-cleaning/stats/summary`, {
      method: 'GET',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    })
    .then(res => res.json())
    .then(data => {
      console.log('[Dashboard] Cleaning stats:', data);
      setCleaningStats({
        repair: data.repair || 0
      });
    })
    .catch(err => console.error('[Dashboard] Error loading cleaning stats:', err));
  }, []);
  
  // Manual reload function
  const handleReload = () => {
    setLoading(true);
    setOrders([]);
    
    const today = new Date().toISOString().split('T')[0];
    const queryParams = `?limit=100&from_date=${today}&to_date=${today}`;
    
    fetch(`${BACKEND_URL}/api/orders${queryParams}`, {
      method: 'GET',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    })
    .then(res => res.json())
    .then(data => {
      console.log('[Dashboard] Manual reload:', data.orders?.length || 0, 'orders');
      setOrders(data.orders || []);
      setLoading(false);
    })
    .catch(err => {
      console.error('[Dashboard] Manual reload error:', err);
      setLoading(false);
    });
  };

  //       :
  //      
  
  // 1.   (awaiting_customer)
  const awaitingOrders = orders; //    status=awaiting_customer  API
  const newOrders = orders; //    KPI
  
  // 2.   (processing) -  
  const processingOrders = decorOrders.filter(o => o.status === 'processing');
  
  // 3.    -       (  )
  const readyOrders = decorOrders.filter(o => 
    o.status === 'processing' || 
    o.status === 'ready' || 
    o.status === 'ready_for_issue'
  );
  
  // Issue Cards ( )   -     :
  const preparationCards = issueCards.filter(c => c.status === 'preparation');
  const readyCards = issueCards.filter(c => 
    c.status === 'ready' || 
    c.status === 'ready_for_issue'
  );
  const issuedCards = issueCards.filter(c => c.status === 'issued');
  
  // 4.   -        (issued/on_rent)
  const returnOrders = decorOrders.filter(o => {
    return (o.status === 'issued' || o.status === 'on_rent');
  });

  const kpis = {
    today: newOrders.length + preparationCards.length + readyCards.length + returnOrders.length,  //   
    revenue: financeData.revenue,  //  Finance API
    deposits: financeData.deposits,  //  Finance API
    problems: 0
  };

  return (
    <div className="min-h-screen bg-white text-slate-900">
      {/* Top Bar */}
      <header className="sticky top-0 z-30 border-b border-slate-200 bg-white/90 backdrop-blur supports-[backdrop-filter]:bg-white/70">
        <div className="mx-auto max-w-7xl px-6 py-3 flex items-center gap-4">
          <div className="flex items-center gap-2">
            <div className="h-8 w-8 rounded-full border border-slate-300 grid place-content-center font-semibold">RH</div>
            <span className="text-lg font-semibold tracking-tight">Rental Hub   </span>
          </div>
          <div className="ml-auto flex items-center gap-3">
            <div className="flex items-center gap-2 px-3 py-1.5 bg-slate-50 rounded-lg border border-slate-200">
              <div className="h-7 w-7 rounded-full bg-gradient-to-br from-blue-500 to-purple-500 grid place-content-center text-white text-xs font-semibold">
                {user?.email?.[0]?.toUpperCase() || 'U'}
              </div>
              <div className="text-sm">
                <div className="font-medium text-slate-900">{user?.email || ''}</div>
                <div className="text-xs text-slate-500">{user?.role === 'admin' ? '' : user?.role === 'manager' ? '' : ''}</div>
              </div>
            </div>
            <button 
              className="h-9 rounded-xl border border-purple-500 bg-purple-50 px-3 text-sm hover:shadow"
              onClick={() => navigate('/settings')}
              title=""
            >
              
            </button>
            <button 
              className="h-9 rounded-xl border border-orange-500 bg-orange-50 px-3 text-sm hover:shadow"
              onClick={handleReload}
            >
               
            </button>
            <button 
              className="h-9 rounded-xl border border-slate-200 px-3 text-sm hover:shadow"
              onClick={() => navigate('/order/new')}
            >
               
            </button>
            <button 
              className="h-9 rounded-xl bg-red-500 px-3 text-sm text-white hover:bg-red-600"
              onClick={() => {
                localStorage.removeItem('token');
                localStorage.removeItem('user');
                navigate('/login');
              }}
            >
              
            </button>
          </div>
        </div>
      </header>

      {/* Filters */}
      <section className="mx-auto max-w-7xl px-6 py-4">
        <div className="grid grid-cols-2 md:grid-cols-5 gap-3">
          <Filter label="">
            <select className="w-full rounded-xl border border-slate-200 px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-teal-400">
              <option></option>
            </select>
          </Filter>
          <Filter label=" ">
            <select 
              className="w-full rounded-xl border border-slate-200 px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-teal-400"
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
            >
              <option></option>
              <option></option>
              <option></option>
              <option> </option>
            </select>
          </Filter>
          <Filter label="">
            <select className="w-full rounded-xl border border-slate-200 px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-teal-400">
              <option></option>
              <option> </option>
              <option></option>
            </select>
          </Filter>
          <Filter label="">
            <input 
              placeholder=" /  / " 
              className="w-full rounded-xl border border-slate-200 px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-teal-400"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </Filter>
          <div className="flex items-end">
            <button
              onClick={() => navigate('/orders-archive')}
              className="w-full rounded-xl border border-slate-300 bg-slate-50 px-3 py-2 text-sm font-medium text-slate-700 hover:bg-slate-100 hover:border-slate-400 transition-colors"
            >
                
            </button>
          </div>
        </div>
      </section>

      {/* KPIs */}
      <section className="mx-auto max-w-7xl px-6">
        <div className="grid grid-cols-2 md:grid-cols-5 gap-3">
          <Kpi title="" value={kpis.today.toString()} note={`${newOrders.length}  / ${preparationCards.length}  / ${readyCards.length}  / ${returnOrders.length} `}/>
          <Kpi title="" value={` ${kpis.revenue.toFixed(0)}`} note=" . "/>
          <Kpi title="  " value={` ${kpis.deposits.toFixed(0)}`} note=" . "/>
          <Kpi 
            title="  " 
            value={cleaningStats.repair.toString()} 
            note="  " 
            tone={cleaningStats.repair > 0 ? "info" : undefined}
          />
          <Kpi title="" value={kpis.problems.toString()} note=" /" tone={kpis.problems > 0 ? "warn" : undefined}/>
        </div>
      </section>

      {/* Boards */}
      <main className="mx-auto max-w-7xl px-6 py-6 grid grid-cols-1 lg:grid-cols-4 gap-6">
        {/*  1:   (    ) */}
        <Column title="  " subtitle="     Email" tone="warning">
          {loading ? (
            <div className="rounded-2xl border border-slate-200 p-4 h-32 bg-slate-50 animate-pulse" />
          ) : awaitingOrders.length > 0 ? (
            <>
              {(showAllAwaiting ? awaitingOrders : awaitingOrders.slice(0, 4)).map(order => (
                <OrderCard 
                  key={order.id}
                  id={order.order_number}
                  name={order.client_name}
                  phone={order.client_phone}
                  rent={` ${order.total_rental?.toFixed(0)}`}
                  deposit={` ${(order.total_deposit || 0).toFixed(0)}`}
                  badge="awaiting"
                  order={order}
                  onDateUpdate={null}
                  onCancelByClient={handleCancelByClient}
                  onClick={() => navigate(`/order/${order.id}/view`)}
                />
              ))}
              {awaitingOrders.length > 4 && !showAllAwaiting && (
                <button 
                  onClick={() => setShowAllAwaiting(true)}
                  className="text-center py-3 text-sm text-blue-600 hover:text-blue-800 font-medium hover:bg-blue-50 rounded-lg transition-colors cursor-pointer"
                >
                  +{awaitingOrders.length - 4}   -  
                </button>
              )}
              {awaitingOrders.length > 4 && showAllAwaiting && (
                <button 
                  onClick={() => setShowAllAwaiting(false)}
                  className="text-center py-3 text-sm text-slate-600 hover:text-slate-800 font-medium hover:bg-slate-50 rounded-lg transition-colors cursor-pointer"
                >
                   
                </button>
              )}
            </>
          ) : (
            <div className="rounded-2xl border border-slate-200 p-8 text-center text-slate-400">
                 
            </div>
          )}
        </Column>

        {/*  2:   /   */}
        <Column title="  " subtitle="  +  " tone="ok">
          {loading ? (
            <div className="rounded-2xl border border-slate-200 p-4 h-32 bg-slate-50 animate-pulse" />
          ) : preparationCards.length > 0 ? (
            <>
              {preparationCards.slice(0, 4).map(card => (
                <OrderCard 
                  key={card.id}
                  id={`#${card.order_id}`}
                  name={card.customer_name || ''}
                  phone={card.customer_phone || ''}
                  rent={` ${card.total_rental?.toFixed(0) || 0}`}
                  deposit={` ${card.deposit_amount?.toFixed(0) || 0}`}
                  badge="preparation"
                  order={card}
                  onDateUpdate={null}
                  onCancelByClient={handleCancelByClient}
                  onClick={() => navigate(`/issue/${card.id}`)}
                />
              ))}
              {preparationCards.length > 4 && (
                <div className="text-center py-2 text-sm text-slate-500">
                  +{preparationCards.length - 4}  
                </div>
              )}
            </>
          ) : (
            <div className="rounded-2xl border border-slate-200 p-8 text-center text-slate-400">
                 
            </div>
          )}
        </Column>

        {/*  3:    */}
        <Column title="   " subtitle="     " tone="ok">
          {loading ? (
            <div className="rounded-2xl border border-slate-200 p-4 h-32 bg-slate-50 animate-pulse" />
          ) : readyCards.length > 0 ? (
            <>
              {readyCards.slice(0, 4).map(card => (
                <OrderCard 
                  key={card.id}
                  id={`#${card.order_id}`}
                  name={card.customer_name || ''}
                  phone={card.customer_phone || ''}
                  rent={` ${card.total_rental?.toFixed(0) || 0}`}
                  deposit={` ${card.deposit_amount?.toFixed(0) || 0}`}
                  badge="ready"
                  order={card}
                  onDateUpdate={null}
                  onCancelByClient={handleCancelByClient}
                  onClick={() => navigate(`/issue/${card.id}`)}
                />
              ))}
              {readyCards.length > 4 && (
                <div className="text-center py-2 text-sm text-slate-500">
                  +{readyCards.length - 4}  
                </div>
              )}
            </>
          ) : (
            <div className="rounded-2xl border border-slate-200 p-8 text-center text-slate-400">
                
            </div>
          )}
        </Column>

        {/*  4:  */}
        <Column title=" " subtitle=" ,   " tone="warn">
          {loading ? (
            <div className="rounded-2xl border border-slate-200 p-4 h-32 bg-slate-50 animate-pulse" />
          ) : returnOrders.length > 0 ? (
            <>
              {(showAllReturns ? returnOrders : returnOrders.slice(0, 4)).map(order => (
                <OrderCard 
                  key={order.id}
                  id={order.order_number}
                  name={order.client_name}
                  phone={order.client_phone}
                  rent={` ${order.total_rental?.toFixed(0)}`}
                  deposit={` ${(order.deposit_held || order.total_deposit || 0).toFixed(0)}`}
                  badge="return"
                  order={order}
                  onClick={() => navigate(`/return/${order.id}`)}
                />
              ))}
              {returnOrders.length > 4 && !showAllReturns && (
                <button 
                  onClick={() => setShowAllReturns(true)}
                  className="text-center py-3 text-sm text-blue-600 hover:text-blue-800 font-medium hover:bg-blue-50 rounded-lg transition-colors cursor-pointer"
                >
                  +{returnOrders.length - 4}   -  
                </button>
              )}
              {returnOrders.length > 4 && showAllReturns && (
                <button 
                  onClick={() => setShowAllReturns(false)}
                  className="text-center py-3 text-sm text-slate-600 hover:text-slate-800 font-medium hover:bg-slate-50 rounded-lg transition-colors cursor-pointer"
                >
                   
                </button>
              )}
            </>
          ) : (
            <div className="rounded-2xl border border-slate-200 p-8 text-center text-slate-400">
                
            </div>
          )}
        </Column>
      </main>

      {/* Bottom nav */}
      <section className="mx-auto max-w-7xl px-6 pb-12">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          <NavCard 
            title=" " 
            description=", , "
            onClick={() => navigate('/finance')}
          />
          <NavCard 
            title=" " 
            description=",  /"
            onClick={() => navigate('/calendar')}
          />
          <NavCard 
            title=" " 
            description=",  "
            onClick={() => navigate('/catalog')}
          />
          <NavCard 
            title="  PRO" 
            description=" , "
            onClick={() => navigate('/damages')}
          />
          <NavCard 
            title="  PRO" 
            description="Kanban , , "
            onClick={() => navigate('/tasks')}
          />
          <NavCard 
            title="  PRO" 
            description=" ,  "
            onClick={() => navigate('/reaudit')}
          />
          <NavCard 
            title=" " 
            description="   "
            onClick={() => navigate('/packing')}
          />
          {user?.role === 'admin' && (
            <NavCard 
              title=" -" 
              description=", , "
              onClick={() => navigate('/admin')}
            />
          )}
        </div>
      </section>

      <footer className="border-t border-slate-200 py-6 text-center text-slate-500 text-sm">
         Rental Hub  manager cabinet
      </footer>
    </div>
  );
}

function Filter({label, children}:{label:string, children:any}){
  return (
    <label className="flex flex-col gap-1 text-sm text-slate-600">
      <span className="font-medium">{label}</span>
      {children}
    </label>
  );
}

function Kpi({title, value, note, tone}:{title:string,value:string,note?:string,tone?:'ok'|'warn'|'info'}){
  const toneMap:any={
    ok:'bg-emerald-50 text-emerald-700',
    warn:'bg-amber-50 text-amber-700',
    info:'bg-slate-50 text-slate-700'
  };
  return (
    <div className={`rounded-2xl border border-slate-200 p-4 shadow-sm ${tone?toneMap[tone]:''}`}>
      <div className="text-sm text-slate-600">{title}</div>
      <div className="mt-1 text-2xl font-semibold tracking-tight tabular-nums">{value}</div>
      {note && <div className="mt-1 text-xs text-slate-500">{note}</div>}
    </div>
  );
}

function Column({title, subtitle, children, tone}:{title:string,subtitle?:string,children:any,tone?:'ok'|'warn'|'info'}){
  const ring:any={ok:'ring-emerald-100',warn:'ring-amber-100',info:'ring-slate-100'}
  return (
    <section className={`rounded-2xl border border-slate-200 p-4 shadow-sm ring-2 ${tone?ring[tone]:"ring-transparent"}`}>
      <header className="mb-3 flex items-center justify-between">
        <div>
          <h3 className="text-base font-semibold leading-none">{title}</h3>
          {subtitle && <p className="mt-1 text-sm text-slate-500">{subtitle}</p>}
        </div>
      </header>
      <div className="grid gap-3">{children}</div>
    </section>
  );
}

function OrderCard({id,name,phone,rent,deposit,badge,onClick,order,onDateUpdate,onCancelByClient}:{id:string,name:string,phone:string,rent:string,deposit:string,badge:'new'|'issue'|'return'|'ready'|'issued'|'awaiting'|'processing'|'preparation',onClick:()=>void,order?:any,onDateUpdate?:(orderId:string,issueDate:string,returnDate:string)=>void,onCancelByClient?:(orderId:number,orderNumber:string)=>void}){
  const map:any={
    new:{label:'',bg:'bg-slate-100 text-slate-700'},
    awaiting:{label:'',bg:'bg-yellow-100 text-yellow-800'},
    processing:{label:' ',bg:'bg-blue-100 text-blue-800'},
    preparation:{label:' ',bg:'bg-purple-100 text-purple-800'},
    issue:{label:'',bg:'bg-emerald-100 text-emerald-800'},
    return:{label:'',bg:'bg-amber-100 text-amber-800'},
    ready:{label:'',bg:'bg-emerald-100 text-emerald-800'},
    issued:{label:'',bg:'bg-green-100 text-green-800'}
  }
  
  // Fallback  badge 
  const badgeInfo = map[badge] || {label: badge, bg: 'bg-slate-100 text-slate-700'}
  
  const [isEditing, setIsEditing] = React.useState(false);
  const [issueDate, setIssueDate] = React.useState(order?.issue_date || '');
  const [returnDate, setReturnDate] = React.useState(order?.return_date || '');
  const [isSaving, setIsSaving] = React.useState(false);
  
  const handleSaveDates = async (e) => {
    e.stopPropagation();
    if (!onDateUpdate) return;
    
    setIsSaving(true);
    try {
      await onDateUpdate(id, issueDate, returnDate);
      setIsEditing(false);
    } catch (error) {
      console.error('Error updating dates:', error);
      alert('  ');
    } finally {
      setIsSaving(false);
    }
  };
  
  const handleCancel = (e) => {
    e.stopPropagation();
    setIsEditing(false);
    setIssueDate(order?.issue_date || '');
    setReturnDate(order?.return_date || '');
  };
  
  return (
    <article className="rounded-2xl border border-slate-200 p-4 shadow-sm hover:shadow-md transition-shadow" onClick={isEditing ? undefined : onClick}>
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center gap-2">
          <span className={`rounded-full px-2.5 py-1 text-xs font-semibold ${badgeInfo.bg}`}>{badgeInfo.label}</span>
          <span className="text-slate-500 text-sm">#{id}</span>
        </div>
        {badge === 'new' && onDateUpdate && !isEditing && (
          <button 
            onClick={(e) => { e.stopPropagation(); setIsEditing(true); }}
            className="text-xs text-slate-500 hover:text-slate-700"
            title=" "
          >
              
          </button>
        )}
      </div>
      <div className="text-sm mb-3">
        <div className="font-medium">{name}</div>
        <div className="text-slate-500">{phone}</div>
      </div>
      
      {/*  (     ) */}
      {badge === 'new' && isEditing ? (
        <div className="mb-3 space-y-2 bg-blue-50 p-3 rounded-xl" onClick={(e) => e.stopPropagation()}>
          <div>
            <label className="text-xs text-slate-600 block mb-1"> </label>
            <input 
              type="date" 
              value={issueDate}
              onChange={(e) => setIssueDate(e.target.value)}
              className="w-full px-2 py-1 text-sm border border-slate-300 rounded"
              disabled={isSaving}
            />
          </div>
          <div>
            <label className="text-xs text-slate-600 block mb-1"> </label>
            <input 
              type="date" 
              value={returnDate}
              onChange={(e) => setReturnDate(e.target.value)}
              className="w-full px-2 py-1 text-sm border border-slate-300 rounded"
              disabled={isSaving}
            />
          </div>
          <div className="flex gap-2">
            <button 
              onClick={handleSaveDates}
              disabled={isSaving}
              className="flex-1 px-3 py-1 text-xs bg-teal-600 text-white rounded hover:bg-teal-700 disabled:opacity-50"
            >
              {isSaving ? ' ...' : ' '}
            </button>
            <button 
              onClick={handleCancel}
              disabled={isSaving}
              className="px-3 py-1 text-xs bg-slate-200 rounded hover:bg-slate-300 disabled:opacity-50"
            >
               
            </button>
          </div>
        </div>
      ) : (
        order?.issue_date || order?.return_date ? (
          <div className="mb-3 text-xs text-slate-600 bg-slate-50 p-2 rounded-xl">
            {order.issue_date && <div> : {order.issue_date}</div>}
            {order.return_date && <div> : {order.return_date}</div>}
          </div>
        ) : null
      )}
      
      <div className="grid grid-cols-2 gap-2 text-sm">
        <div className="rounded-xl bg-slate-50 px-3 py-2">
          <div className="text-slate-500"></div>
          <div className="font-semibold tabular-nums">{rent}</div>
        </div>
        <div className="rounded-xl bg-slate-50 px-3 py-2">
          <div className="text-slate-500"></div>
          <div className="font-semibold tabular-nums">{deposit}</div>
        </div>
      </div>
      
      {/*  " "     */}
      {onCancelByClient && ['awaiting', 'processing', 'preparation', 'ready'].includes(badge) && (
        <button
          onClick={(e) => {
            e.stopPropagation();
            onCancelByClient(order?.order_id, id);
          }}
          className="mt-2 w-full text-xs text-rose-600 border border-rose-300 rounded-lg px-2 py-1.5 hover:bg-rose-50 transition-colors"
        >
            
        </button>
      )}
    </article>
  );
}

function OrderCardWithArchive({id,name,phone,rent,deposit,badge,onClick,order,onArchive}:{id:string,name:string,phone:string,rent:string,deposit:string,badge:string,onClick:()=>void,order?:any,onArchive?:(orderId:number,orderNumber:string)=>void}){
  return (
    <article onClick={onClick} className="relative cursor-pointer rounded-xl border border-slate-200 bg-white p-3 transition hover:border-teal-400 hover:shadow-lg">
      <div className="mb-2 flex items-center justify-between">
        <div className="text-sm font-semibold text-slate-900">{id}</div>
      </div>
      
      <div className="mb-2 text-xs text-slate-500">{name}</div>
      <div className="mb-3 text-xs text-slate-500">{phone}</div>
      
      <div className="grid grid-cols-2 gap-2 text-sm">
        <div className="rounded-xl bg-slate-50 px-3 py-2">
          <div className="text-slate-500"></div>
          <div className="font-semibold tabular-nums">{rent}</div>
        </div>
        <div className="rounded-xl bg-slate-50 px-3 py-2">
          <div className="text-slate-500"></div>
          <div className="font-semibold tabular-nums">{deposit}</div>
        </div>
      </div>
      
      {onArchive && (
        <button
          onClick={(e) => {
            e.stopPropagation();
            onArchive(order?.order_id, id);
          }}
          className="mt-2 w-full text-xs text-slate-600 border border-slate-300 rounded-lg px-2 py-1.5 hover:bg-slate-50 transition-colors"
        >
            
        </button>
      )}
    </article>
  );
}

function NavCard({title, description, onClick}:{title:string, description:string, onClick:()=>void}){
  return (
    <article 
      className="rounded-2xl border border-slate-200 p-6 shadow-sm hover:shadow-md transition-shadow cursor-pointer"
      onClick={onClick}
    >
      <h3 className="text-lg font-semibold mb-2">{title}</h3>
      <p className="text-sm text-slate-500">{description}</p>
    </article>
  );
}
