function Checklist({check, setCheck}){
  return (
    <Card title="  " right={<Badge tone={check.photos_before && check.docs_printed ? 'green':'amber'}>{check.photos_before && check.docs_printed ? '' : ' '}</Badge>}>
      <div className="grid gap-3 md:grid-cols-2">
        <label className="flex items-center gap-2 text-sm cursor-pointer"><input type="checkbox" checked={check.stretch} onChange={e=>setCheck({...check, stretch:e.target.checked})}/> </label>
        <label className="flex items-center gap-2 text-sm cursor-pointer"><input type="checkbox" checked={check.labels} onChange={e=>setCheck({...check, labels:e.target.checked})}/> /</label>
        <label className="flex items-center gap-2 text-sm cursor-pointer"><input type="checkbox" checked={check.photos_before} onChange={e=>setCheck({...check, photos_before:e.target.checked})}/>   ( )</label>
        <label className="flex items-center gap-2 text-sm cursor-pointer"><input type="checkbox" checked={check.docs_printed} onChange={e=>setCheck({...check, docs_printed:e.target.checked})}/>  </label>
      </div>
    </Card>
  )
}

function Documents({orderId, docs, setDocs}){
  const handleDownloadPicklist = async () => {
    try {
      window.open(`${BACKEND_URL}/api/pdf/pick-list/${orderId}`, '_blank')
      setDocs({...docs, waybill:true})
    } catch(e){
      alert('  pick-list')
    }
  }
  
  const handleDownloadInvoice = async () => {
    try {
      window.open(`${BACKEND_URL}/api/pdf/invoice/${orderId}`, '_blank')
      setDocs({...docs, act:true})
    } catch(e){
      alert('  ')
    }
  }
  
  return (
    <Card title="">
      <div className="flex flex-wrap items-center gap-2 text-sm">
        <PillButton tone='blue' onClick={handleDownloadPicklist}> (PDF)</PillButton>
        <PillButton tone='blue' onClick={handleDownloadInvoice}> (PDF)</PillButton>
        <Badge tone={docs.waybill?'green':'slate'}>: {docs.waybill? '':''}</Badge>
        <Badge tone={docs.act?'green':'slate'}>: {docs.act? '':''}</Badge>
      </div>
    </Card>
  )
}

function Notes({notes, setNotes}){
  return (
    <Card title="">
      <textarea value={notes||''} onChange={e=>setNotes(e.target.value)} className="w-full rounded-xl border p-3 text-sm" rows={3} placeholder=" "/>
    </Card>
  )
}

function Timeline({events}){
  return (
    <Card title="">
      <ol className="space-y-2 text-sm max-h-60 overflow-auto">
        {events.map((e,i)=> (
          <li key={i} className="flex items-start gap-2">
            <div className={cls('mt-1 h-2 w-2 rounded-full flex-shrink-0', e.tone==='green'?'bg-emerald-500':e.tone==='red'?'bg-rose-500':'bg-blue-500')} />
            <div className="flex-1">
              <div className="font-medium">{e.title}</div>
              <div className="flex items-center gap-2 text-xs text-slate-500">
                <span>{e.when}</span>
                {e.manager && (
                  <>
                    <span></span>
                    <span className="text-blue-600 font-medium">{e.manager}</span>
                  </>
                )}
              </div>
            </div>
          </li>
        ))}
      </ol>
    </Card>
  )
}

