/*********** Monthly (tab 3) ***********/
function MonthlyArchive({rows}){
  const groups = useMemo(()=>{
    const m = new Map()
    rows.forEach(r=>{
      const key = (r.date||todayISO()).slice(0,7)
      const g = m.get(key) || {month:key, debit:0, credit:0, count:0}
      g.debit += (r.debit||0); g.credit += (r.credit||0); g.count += 1
      m.set(key,g)
    })
    return Array.from(m.values()).sort((a,b)=> a.month<b.month?1:-1)
  },[rows])
  return (
    <div className="space-y-3">
      {groups.map(g=> (
        <div key={g.month} className="rounded-xl border p-3 flex items-center justify-between">
          <div>
            <div className="font-semibold">{g.month}</div>
            <div className="text-xs text-slate-500">: {g.count}</div>
          </div>
          <div className="text-sm">
            <span className="mr-4">:  {fmtUA(g.debit)}</span>
            <span>:  {fmtUA(g.credit)}</span>
          </div>
        </div>
      ))}
    </div>
  )
}

/*********** main ***********/
export default function FinanceCabinet(){
  const [rows, setRows] = useState([])
  const [loading, setLoading] = useState(true)
  const [tab, setTab] = useState('orders')
  const [expandedOrderId, setExpandedOrderId] = useState(null)
  const [scannerOpen, setScannerOpen] = useState(false)

  // Load all transactions
  useEffect(()=>{
    loadTransactions()
  },[])

  const loadTransactions = async ()=>{
    try {
      setLoading(true)
      const res = await axios.get(`${BACKEND_URL}/api/manager/finance/ledger`)
      setRows(res.data.map(r=>({
        id: r.id,
        date: r.date,
        order_id: r.order_id,
        type: r.type,
        title: r.title,
        method: r.payment_method,
        debit: r.debit || 0,
        credit: r.credit || 0,
        amount: r.amount || 0,
        currency: r.currency || 'UAH',
        status: r.status,
        counterparty: r.counterparty,
        client_name: r.client_name,
        expected_deposit: r.expected_deposit || 0,
        manager_comment: r.manager_comment || '',
        damage_fee: r.damage_fee || 0,
        order_status: r.order_status || null
      })))
    } catch(e){
      console.error('Error loading transactions:', e)
      alert('   ')
    } finally {
      setLoading(false)
    }
  }

  const addPayment = async (orderId, p)=>{
    try {
      const payload = {
        order_id: orderId,
        transaction_type: 'payment',
        payment_method: p.method,
        amount: Number(p.amount||0),
        currency: 'UAH',
        status: 'completed',
        description: ` (${p.method})`,
        notes: p.note||''
      }
      await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, payload)
      await loadTransactions()
      alert(' !')
    } catch(e){
      console.error('Error adding payment:', e)
      alert('   ')
    }
  }

  const addDeposit = async (orderId, dep)=>{
    try {
      const payload = {
        order_id: orderId,
        transaction_type: 'deposit_hold',
        payment_method: 'cash',
        amount: Number(dep.amount||0),
        currency: dep.code,
        status: 'held',
        description: ` (${dep.code})`,
        notes: ''
      }
      await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, payload)
      await loadTransactions()
      alert(' !')
    } catch(e){
      console.error('Error adding deposit:', e)
      alert('   ')
    }
  }

  const writeoff = async (orderId, heldByCurrency, dueAmount)=>{
    try {
      //       
      let remaining = dueAmount
      const currencies = Object.entries(heldByCurrency).filter(([_, amt]) => amt > 0)
      
      if (currencies.length === 0) {
        alert('  ')
        return
      }
      
      // : UAH,   
      const sorted = currencies.sort((a, b) => {
        if (a[0] === 'UAH') return -1
        if (b[0] === 'UAH') return 1
        return 0
      })
      
      const writeoffs = []
      
      for (const [currency, amount] of sorted) {
        if (remaining <= 0) break
        
        const toWrite = Math.min(amount, remaining)
        const symbol = currency === 'UAH' ? '' : currency === 'USD' ? '$' : ''
        
        // Writeoff record
        await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, {
          order_id: orderId,
          transaction_type: 'deposit_writeoff',
          amount: toWrite,
          currency: currency,
          status: 'completed',
          description: '  ',
          notes: ` ${symbol}${toWrite}`
        })
        
        // Payment from deposit
        await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, {
          order_id: orderId,
          transaction_type: 'payment',
          payment_method: 'deposit',
          amount: toWrite,
          currency: currency,
          status: 'completed',
          description: '   '
        })
        
        writeoffs.push(`${symbol}${toWrite} ${currency}`)
        remaining -= toWrite
      }
      
      await loadTransactions()
      alert(`  :\n${writeoffs.join('\n')}`)
    } catch(e){
      console.error('Error writeoff:', e)
      alert('  ')
    }
  }

  const releaseDeposit = async (orderId, heldByCurrency)=>{
    try {
      //    
      const currencies = Object.entries(heldByCurrency).filter(([_, amt]) => amt > 0)
      
      if (currencies.length === 0) {
        alert('  ')
        return
      }
      
      for (const [currency, amount] of currencies) {
        const symbol = currency === 'UAH' ? '' : currency === 'USD' ? '$' : ''
        await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, {
          order_id: orderId,
          transaction_type: 'deposit_release',
          amount: amount,
          currency: currency,
          status: 'completed',
          description: ' ',
          notes: ` ${symbol}${amount}`
        })
      }
      
      await loadTransactions()
      alert(` !\n${currencies.map(([c, a]) => `${c === 'UAH' ? '' : c === 'USD' ? '$' : ''}${a}`).join(' + ')}`)
    } catch(e){
      console.error('Error release:', e)
      alert('  ')
    }
  }

  const refund = async (orderId, amount)=>{
    try {
      await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, {
        order_id: orderId,
        transaction_type: 'refund',
        amount: Number(amount),
        currency: 'UAH',
        status: 'completed',
        description: '  ( )',
        notes: ` ${amount}   `
      })
      
      await loadTransactions()
      alert(`  ${amount} !`)
    } catch(e){
      console.error('Error refund:', e)
      alert('   ')
    }
  }


  const addDamage = async (orderId, dmg)=>{
    try {
      await axios.post(`${BACKEND_URL}/api/manager/finance/transactions`, {
        order_id: orderId,
        transaction_type: 'damage',
        amount: Number(dmg.amount||0),
        currency: 'UAH',
        status: 'unpaid',
        description: `: ${dmg.note||''}`
      })
      await loadTransactions()
      alert(' !')
    } catch(e){
      console.error('Error adding damage:', e)
      alert('   ')
    }
  }

  const deleteOrder = async (orderId)=>{
    if(!window.confirm(`  #${orderId}?     '  (Issue/Return).`)) return
    
    try {
      await axios.delete(`${BACKEND_URL}/api/orders/${orderId}`)
      await loadTransactions()
      setExpandedOrderId(null)
      alert(' !')
    } catch(e){
      console.error('Error deleting order:', e)
      alert('   ')
    }
  }

  const rowsFiltered = useMemo(()=> rows.sort((a,b)=> (b.date||'').localeCompare(a.date||'')),[rows])
  
  // Get unique order IDs
  const orderIds = useMemo(()=>{
    const ids = new Set(rows.map(r=>r.order_id))
    return Array.from(ids).sort((a,b)=>b-a)
  },[rows])

  if(loading) return <div className="flex items-center justify-center h-screen"><div className="text-xl">...</div></div>

  return (
    <div className="mx-auto max-w-7xl p-6 space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold"> </h1>
        <div className="flex gap-2">
          <button 
            onClick={() => setScannerOpen(true)}
            className="rounded-full px-3 py-1 text-sm bg-blue-600 text-white hover:bg-blue-700 flex items-center gap-1"
          >
             
          </button>
          {['orders','ledger','monthly'].map(t => (
            <button key={t} onClick={()=>{setTab(t); setExpandedOrderId(null)}} className={cls('rounded-full px-3 py-1 text-sm', tab===t? 'bg-slate-900 text-white':'bg-slate-200 text-slate-800')}>
              {t==='orders'?'':t==='ledger'?'':''}
            </button>
          ))}
        </div>
      </div>
      
      {/* Barcode Scanner */}
      <BarcodeScanner
        isOpen={scannerOpen}
        onClose={() => setScannerOpen(false)}
        onScan={(code) => {
          console.log('[FinanceCabinet] Scanned:', code)
          // Extract order ID from code (assuming format like "OC-6996" or just "6996")
          const orderId = parseInt(code.replace(/[^0-9]/g, ''))
          if (orderId && !isNaN(orderId)) {
            // Find and expand order
            if (orderIds.includes(orderId)) {
              setExpandedOrderId(orderId)
              // Scroll to order
              setTimeout(() => {
                const element = document.querySelector(`[data-order-id="${orderId}"]`)
                if (element) {
                  element.scrollIntoView({ behavior: 'smooth', block: 'center' })
                }
              }, 100)
            } else {
              alert(` #${orderId}     `)
            }
          } else {
            alert(`      : ${code}`)
          }
        }}
        title=" "
      />

      {tab==='orders' && (
        <div className="space-y-4">
          <Card title={`  (${orderIds.length})`} right={<Badge tone='slate'>    </Badge>}>
            <div className="text-xs text-slate-500 mb-3">     </div>
          </Card>

          {orderIds.map(orderId=> (
            <div key={orderId} data-order-id={orderId}>
              <OrderListItem 
                orderId={orderId} 
                rows={rowsFiltered} 
                onClick={()=>setExpandedOrderId(expandedOrderId===orderId? null : orderId)}
                isExpanded={expandedOrderId===orderId}
              />
              
              {expandedOrderId===orderId && (
                <div className="mt-4">
                  <OrderFinanceCard
                    orderId={orderId}
                    rows={rowsFiltered}
                    onAddPayment={addPayment}
                    onAddDeposit={addDeposit}
                    onWriteoff={writeoff}
                    onReleaseDeposit={releaseDeposit}
                    onAddDamage={addDamage}
                    onRefund={refund}
                    onCollapse={()=>setExpandedOrderId(null)}
                    onDelete={deleteOrder}
                  />
                </div>
              )}
            </div>
          ))}

          {orderIds.length === 0 && (
            <div className="text-center py-12 text-slate-500">
              <div className="text-lg">  </div>
              <div className="text-sm mt-2"> '   </div>
            </div>
          )}
        </div>
      )}

      {tab==='ledger' && (
        <Card title="  ">
          <LedgerTable rows={rowsFiltered} />
        </Card>
      )}

      {tab==='monthly' && (
        <Card title="   "><MonthlyArchive rows={rowsFiltered} /></Card>
      )}
    </div>
  )
}
